import React from 'react';
import Routes from './routes';
import './components/Rodada.css';
import './global.css';
import useStayAwake from "use-stay-awake";

function App() {

  const device = useStayAwake();

  return (
    < div className="app"
      onClick={() => {
        if (device.canSleep)
          device.preventSleeping();
      }}>
      <Routes />
    </div >
  );
}

export default App;